body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: hidden;
    background-color: #1a1a1a;
}

.grid-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgba(255, 255, 255, 0.064) 1px, transparent 1px), linear-gradient(90deg, rgba(255, 255, 255, 0.064) 1px, transparent 1px);
    background-size: 40px 40px, 40px 40px;
    animation: moveGrid 5s linear infinite;
}

@keyframes moveGrid {
    0% {
        background-position: 0 0;
    }

    100% {
        background-position: 40px 40px;
    }
}